<template>
  <div>
    <template v-for="(item, index) in menus">
      <div style="text-align: center;" :key="index">
        <Dropdown transfer v-if="item.children.length !== 1" placement="right-start" :key="index" @on-click="changeMenu">
          <Button style="width: 70px;margin-left: -5px;padding:10px 0;" type="text">
            <Icon :size="20" :color="iconColor" :type="item.icon"></Icon>
          </Button>
          <DropdownMenu style="width: 200px;" slot="list">
            <template v-for="(child, i) in item.children">
              <DropdownItem :name="child.name" :key="i">
                <Icon :type="child.icon"></Icon>
                <span style="padding-left:10px;">{{ child.meta.title }}</span>
              </DropdownItem>
            </template>
          </DropdownMenu>
        </Dropdown>
        <Dropdown transfer v-else placement="right-start" :key="index" @on-click="changeMenu">
          <Button @click="changeMenu(item.children[0].name)" style="width: 70px;margin-left: -5px;padding:10px 0;" type="text">
            <Icon :size="20" :color="iconColor" :type="item.icon"></Icon>
          </Button>
          <DropdownMenu style="width: 200px;" slot="list">
            <DropdownItem :name="item.children[0].name" :key="'d' + index">
              <Icon :type="item.icon"></Icon>
              <span style="padding-left:10px;">{{ item.children[0].meta.title }}</span>
            </DropdownItem>
          </DropdownMenu>
        </Dropdown>
      </div>
    </template>
  </div>
</template>

<script>

export default {
  name: 'SidebarMenuShrink',
  props: {
    menus: {
      type: Array
    },
    iconColor: {
      type: String,
      default: 'white'
    }
  },
  computed: {
    currentPageName () {
      return this.$route.name
    }
  },
  methods: {
    changeMenu (name) {
      this.$router.push({ name })
    }
  }
}
</script>



